'use strict'
import { Header } from "./components/header/header"; //    
import { Buttons } from './components/buttons/buttons';


// console.log(Buttons, Header)


$(document).ready(function ($) {

    //     
    if ($('.btn').length) {
        Buttons.init()
        // console.log(BtnsInit)
    }
    if (('header').length) {
        Header.initScroll()
        // console.log(theme.Header)
    }
})//  ready


$(window).on('load', function () {
    setTimeout(() => {
        RestartScroll()
    }, 10);
})

let RestartScroll = function () {
    //      window.scrollX  window.scrollY  scrollX  scrollY
    window.addEventListener('beforeunload', () => {
        sessionStorage.setItem('scrollPosition', window.scrollY);
    });
    //    scrollX  scrollY  localStorage ( 0,0     )
    const lastScrollPosition = sessionStorage.getItem('scrollPosition');
    // console.log(sessionStorage)
    if (lastScrollPosition) {
        window.scroll(0, 0);
        $('html, body').stop().animate({
            scrollTop: lastScrollPosition
        }, 200)
        // window.scroll(0, lastScrollPosition);
        sessionStorage.removeItem('scrollPosition');
    }
    // console.log(sessionStorage)
    // $(window).trigger('scroll')
    // console.log()
}


